<template>
  <main>
    <template>
      <v-row justify="space-around">
        <v-col cols="auto">
          <v-dialog transition="dialog-top-transition" max-width="600">
            <template v-slot:activator="{ on, attrs }">
              <span color="none" v-bind="attrs" v-on="on">{{
                dataList.length / 2 + text
              }}</span>
            </template>
            <template v-slot:default="dialog">
              <v-card>
                <v-toolbar color="#2ac187" dark>{{ text }} list</v-toolbar>
                <v-list three-line>
                  <template v-for="(data, index) in dataList">
                    <v-subheader
                      v-if="data.header"
                      :key="data.header"
                      v-text="data.header"
                    />
                    <v-divider
                      v-else-if="data.divider"
                      :key="data.followId"
                      :inset="data.inset"
                    />
                    <v-list-item v-else :key="index">
                      <v-list-item-avatar>
                        <v-img
                          :src="
                            require(`@/assets/image/user/${data.profileImg}`)
                          "
                        ></v-img>
                      </v-list-item-avatar>
                      <v-list-item-content>
                        <v-list-item-title
                          v-if="text == 'follower'"
                          v-html="data.followingId"
                        ></v-list-item-title>
                        <v-list-item-title
                          v-else-if="text == 'following'"
                          v-html="data.followerId"
                        ></v-list-item-title>
                        <v-list-item-subtitle
                          v-html="data.nickname"
                        ></v-list-item-subtitle>
                      </v-list-item-content>
                    </v-list-item>
                  </template>
                </v-list>
                <v-card-actions class="justify-end">
                  <v-btn text @click="dialog.value = false"> Close </v-btn>
                </v-card-actions>
              </v-card>
            </template>
          </v-dialog>
        </v-col>
      </v-row>
    </template>
  </main>
</template>
